import React, { useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  CardActions,
  Button,
  TextField,
  Grid,
  Paper,
  Slider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Chip,
  Switch,
  FormControlLabel,
  Stepper,
  Step,
  StepLabel,
  StepContent,
  Alert,
  Divider,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  IconButton
} from '@mui/material';
import {
  Route,
  Terrain,
  Speed,
  Schedule,
  LocationOn,
  DirectionsBike,
  Add,
  Remove,
  CheckCircle,
  Warning,
  Info
} from '@mui/icons-material';

const CreateRideScreen = () => {
  const [activeStep, setActiveStep] = useState(0);
  const [rideConfig, setRideConfig] = useState({
    // Basic Settings
    name: '',
    description: '',
    rideType: 'recreation',
    
    // Distance and Duration
    targetDistance: 20,
    maxDistance: 50,
    targetDuration: 60,
    maxDuration: 180,
    
    // Elevation Settings
    targetElevation: 200,
    maxElevation: 1000,
    elevationPreference: 'moderate', // low, moderate, high
    
    // Route Preferences
    avoidHighways: true,
    avoidFreeways: true,
    preferBikePaths: true,
    avoidBusyRoads: false,
    
    // Advanced Settings
    startLocation: '',
    endLocation: '',
    waypoints: [],
    
    // Algorithm Settings (for future DFS implementation)
    algorithmType: 'dfs', // dfs, dijkstra, a-star
    maxNodes: 1000,
    searchRadius: 5000, // meters
    roadTypeWeights: {
      bikePath: 1.0,
      residential: 1.2,
      arterial: 1.5,
      highway: 2.0,
      freeway: 3.0
    }
  });

  const rideTypes = [
    { value: 'commute', label: 'Commute' },
    { value: 'recreation', label: 'Recreation' },
    { value: 'training', label: 'Training' },
    { value: 'adventure', label: 'Adventure' },
    { value: 'family', label: 'Family' }
  ];

  const elevationPreferences = [
    { value: 'low', label: 'Flat (0-100m)' },
    { value: 'moderate', label: 'Moderate (100-500m)' },
    { value: 'high', label: 'Hilly (500m+)' }
  ];

  const steps = [
    'Basic Information',
    'Distance & Duration',
    'Elevation & Terrain',
    'Route Preferences',
    'Advanced Settings',
    'Algorithm Configuration'
  ];

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const updateConfig = (field, value) => {
    setRideConfig(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const updateNestedConfig = (parent, field, value) => {
    setRideConfig(prev => ({
      ...prev,
      [parent]: {
        ...prev[parent],
        [field]: value
      }
    }));
  };

  const renderBasicInfo = () => (
    <Box>
      <TextField
        fullWidth
        label="Ride Name"
        value={rideConfig.name}
        onChange={(e) => updateConfig('name', e.target.value)}
        sx={{ mb: 2 }}
      />
      <TextField
        fullWidth
        label="Description"
        multiline
        rows={3}
        value={rideConfig.description}
        onChange={(e) => updateConfig('description', e.target.value)}
        sx={{ mb: 2 }}
      />
      <FormControl fullWidth>
        <InputLabel>Ride Type</InputLabel>
        <Select
          value={rideConfig.rideType}
          onChange={(e) => updateConfig('rideType', e.target.value)}
        >
          {rideTypes.map((type) => (
            <MenuItem key={type.value} value={type.value}>
              {type.label}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Box>
  );

  const renderDistanceDuration = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
        Target Distance: {rideConfig.targetDistance} km
      </Typography>
      <Slider
        value={rideConfig.targetDistance}
        onChange={(e, value) => updateConfig('targetDistance', value)}
        min={5}
        max={100}
        step={5}
        marks={[
          { value: 10, label: '10km' },
          { value: 25, label: '25km' },
          { value: 50, label: '50km' },
          { value: 75, label: '75km' }
        ]}
        sx={{ mb: 3 }}
      />
      
      <Typography variant="h6" gutterBottom>
        Maximum Distance: {rideConfig.maxDistance} km
      </Typography>
      <Slider
        value={rideConfig.maxDistance}
        onChange={(e, value) => updateConfig('maxDistance', value)}
        min={rideConfig.targetDistance}
        max={200}
        step={5}
        sx={{ mb: 3 }}
      />

      <Typography variant="h6" gutterBottom>
        Target Duration: {rideConfig.targetDuration} minutes
      </Typography>
      <Slider
        value={rideConfig.targetDuration}
        onChange={(e, value) => updateConfig('targetDuration', value)}
        min={15}
        max={300}
        step={15}
        marks={[
          { value: 30, label: '30min' },
          { value: 60, label: '1h' },
          { value: 120, label: '2h' },
          { value: 180, label: '3h' }
        ]}
      />
    </Box>
  );

  const renderElevation = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
        Target Elevation Gain: {rideConfig.targetElevation}m
      </Typography>
      <Slider
        value={rideConfig.targetElevation}
        onChange={(e, value) => updateConfig('targetElevation', value)}
        min={0}
        max={2000}
        step={50}
        marks={[
          { value: 100, label: '100m' },
          { value: 500, label: '500m' },
          { value: 1000, label: '1000m' },
          { value: 1500, label: '1500m' }
        ]}
        sx={{ mb: 3 }}
      />

      <FormControl fullWidth sx={{ mb: 2 }}>
        <InputLabel>Elevation Preference</InputLabel>
        <Select
          value={rideConfig.elevationPreference}
          onChange={(e) => updateConfig('elevationPreference', e.target.value)}
        >
          {elevationPreferences.map((pref) => (
            <MenuItem key={pref.value} value={pref.value}>
              {pref.label}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      <Alert severity="info" sx={{ mb: 2 }}>
        The algorithm will prioritize routes that match your elevation preferences while staying within your target range.
      </Alert>
    </Box>
  );

  const renderRoutePreferences = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
        Road Type Preferences
      </Typography>
      
      <List>
        <ListItem>
          <ListItemIcon>
            <DirectionsBike />
          </ListItemIcon>
          <ListItemText 
            primary="Prefer Bike Paths" 
            secondary="Prioritize dedicated cycling infrastructure"
          />
          <Switch
            checked={rideConfig.preferBikePaths}
            onChange={(e) => updateConfig('preferBikePaths', e.target.checked)}
          />
        </ListItem>
        
        <ListItem>
          <ListItemIcon>
            <Warning />
          </ListItemIcon>
          <ListItemText 
            primary="Avoid Highways" 
            secondary="Exclude highway segments from routes"
          />
          <Switch
            checked={rideConfig.avoidHighways}
            onChange={(e) => updateConfig('avoidHighways', e.target.checked)}
          />
        </ListItem>
        
        <ListItem>
          <ListItemIcon>
            <Warning />
          </ListItemIcon>
          <ListItemText 
            primary="Avoid Freeways" 
            secondary="Exclude freeway segments from routes"
          />
          <Switch
            checked={rideConfig.avoidFreeways}
            onChange={(e) => updateConfig('avoidFreeways', e.target.checked)}
          />
        </ListItem>
        
        <ListItem>
          <ListItemIcon>
            <Speed />
          </ListItemIcon>
          <ListItemText 
            primary="Avoid Busy Roads" 
            secondary="Minimize time on high-traffic roads"
          />
          <Switch
            checked={rideConfig.avoidBusyRoads}
            onChange={(e) => updateConfig('avoidBusyRoads', e.target.checked)}
          />
        </ListItem>
      </List>

      <Alert severity="info" sx={{ mt: 2 }}>
        These preferences will be used by the DFS algorithm to weight different road types and avoid unwanted segments.
      </Alert>
    </Box>
  );

  const renderAdvancedSettings = () => (
    <Box>
      <TextField
        fullWidth
        label="Start Location (Optional)"
        value={rideConfig.startLocation}
        onChange={(e) => updateConfig('startLocation', e.target.value)}
        sx={{ mb: 2 }}
        InputProps={{
          startAdornment: <LocationOn sx={{ mr: 1, color: 'text.secondary' }} />
        }}
      />
      
      <TextField
        fullWidth
        label="End Location (Optional)"
        value={rideConfig.endLocation}
        onChange={(e) => updateConfig('endLocation', e.target.value)}
        sx={{ mb: 2 }}
        InputProps={{
          startAdornment: <LocationOn sx={{ mr: 1, color: 'text.secondary' }} />
        }}
      />

      <Alert severity="warning" sx={{ mb: 2 }}>
        <Typography variant="body2">
          <strong>Note:</strong> If no start/end locations are specified, the algorithm will use your current location as the starting point and generate a loop route.
        </Typography>
      </Alert>

      <Typography variant="h6" gutterBottom>
        Waypoints
      </Typography>
      <Typography variant="body2" color="text.secondary" paragraph>
        Add specific locations you want the route to pass through. The DFS algorithm will ensure these waypoints are included in the optimal path.
      </Typography>
      
      <Button variant="outlined" startIcon={<Add />} fullWidth>
        Add Waypoint
      </Button>
    </Box>
  );

  const renderAlgorithmConfig = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
        DFS Algorithm Configuration
      </Typography>
      
      <Alert severity="info" sx={{ mb: 3 }}>
        <Typography variant="body2">
          <strong>DFS (Depth-First Search) Algorithm:</strong> This algorithm will convert Google Maps street data into a graph of nodes (intersections) and edges (streets), then use DFS to find optimal cycling routes based on your preferences.
        </Typography>
      </Alert>

      <Grid container spacing={2}>
        <Grid item xs={12} sm={6}>
          <FormControl fullWidth>
            <InputLabel>Algorithm Type</InputLabel>
            <Select
              value={rideConfig.algorithmType}
              onChange={(e) => updateConfig('algorithmType', e.target.value)}
            >
              <MenuItem value="dfs">Depth-First Search</MenuItem>
              <MenuItem value="dijkstra">Dijkstra's Algorithm</MenuItem>
              <MenuItem value="a-star">A* Search</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            label="Search Radius (meters)"
            type="number"
            value={rideConfig.searchRadius}
            onChange={(e) => updateConfig('searchRadius', parseInt(e.target.value))}
          />
        </Grid>
        
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            label="Max Graph Nodes"
            type="number"
            value={rideConfig.maxNodes}
            onChange={(e) => updateConfig('maxNodes', parseInt(e.target.value))}
          />
        </Grid>
      </Grid>

      <Typography variant="h6" gutterBottom sx={{ mt: 3 }}>
        Road Type Weights
      </Typography>
      <Typography variant="body2" color="text.secondary" paragraph>
        Lower weights = higher preference. The algorithm will use these weights to calculate the optimal path.
      </Typography>

      <Grid container spacing={2}>
        {Object.entries(rideConfig.roadTypeWeights).map(([roadType, weight]) => (
          <Grid item xs={12} sm={6} key={roadType}>
            <Typography variant="body2" gutterBottom>
              {roadType.charAt(0).toUpperCase() + roadType.slice(1).replace(/([A-Z])/g, ' $1')}
            </Typography>
            <Slider
              value={weight}
              onChange={(e, value) => updateNestedConfig('roadTypeWeights', roadType, value)}
              min={0.5}
              max={5.0}
              step={0.1}
              marks={[
                { value: 1.0, label: '1.0' },
                { value: 2.5, label: '2.5' },
                { value: 5.0, label: '5.0' }
              ]}
            />
          </Grid>
        ))}
      </Grid>

      <Alert severity="success" sx={{ mt: 3 }}>
        <Typography variant="body2">
          <strong>Ready for Implementation:</strong> This configuration will be used by the backend DFS algorithm to generate optimal cycling routes from Google Maps data.
        </Typography>
      </Alert>
    </Box>
  );

  const getStepContent = (step) => {
    switch (step) {
      case 0: return renderBasicInfo();
      case 1: return renderDistanceDuration();
      case 2: return renderElevation();
      case 3: return renderRoutePreferences();
      case 4: return renderAdvancedSettings();
      case 5: return renderAlgorithmConfig();
      default: return 'Unknown step';
    }
  };

  return (
    <Box sx={{ padding: 2, paddingBottom: 8 }}>
      {/* Header */}
      <Box sx={{ textAlign: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1" gutterBottom sx={{ fontWeight: 'bold', color: 'primary.main' }}>
          Create a Ride
        </Typography>
        <Typography variant="subtitle1" color="text.secondary">
          Configure your ideal cycling route with advanced DFS algorithm
        </Typography>
      </Box>

      {/* Stepper */}
      <Paper elevation={2} sx={{ p: 2, mb: 3 }}>
        <Stepper activeStep={activeStep} orientation="horizontal">
          {steps.map((label, index) => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>
      </Paper>

      {/* Step Content */}
      <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
        {getStepContent(activeStep)}
      </Paper>

      {/* Navigation */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
        <Button
          disabled={activeStep === 0}
          onClick={handleBack}
          sx={{ mr: 1 }}
        >
          Back
        </Button>
        <Box>
          {activeStep === steps.length - 1 ? (
            <Button variant="contained" onClick={handleReset} sx={{ mr: 1 }}>
              Reset
            </Button>
          ) : null}
          <Button
            variant="contained"
            onClick={handleNext}
            disabled={activeStep === steps.length - 1}
          >
            {activeStep === steps.length - 1 ? 'Generate Route' : 'Next'}
          </Button>
        </Box>
      </Box>

      {/* Configuration Summary */}
      {activeStep > 0 && (
        <Paper elevation={1} sx={{ p: 2, mt: 3, backgroundColor: 'grey.50' }}>
          <Typography variant="h6" gutterBottom>
            Configuration Summary
          </Typography>
          <Grid container spacing={1}>
            <Grid item xs={6}>
              <Typography variant="body2" color="text.secondary">
                Distance: {rideConfig.targetDistance}km
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="body2" color="text.secondary">
                Duration: {rideConfig.targetDuration}min
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="body2" color="text.secondary">
                Elevation: {rideConfig.targetElevation}m
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="body2" color="text.secondary">
                Type: {rideTypes.find(t => t.value === rideConfig.rideType)?.label}
              </Typography>
            </Grid>
          </Grid>
        </Paper>
      )}
    </Box>
  );
};

export default CreateRideScreen;

